import React from "react";
import { connect } from "react-redux";
import MenuItem from '../MenuItem/MenuItem';

function PizzaMenu(props) {
    return (
      <>
        {props.pizzaMenu.map(item => {
          return <MenuItem src={item.src} name={item.name} size={item.size} minPrice={item.minPrice} mediumPrice={item.mediumPrice} maxPrice={item.maxPrice}
          description={item.description} onSale={item.onSale} itemIsPizza={item.itemIsPizza} onPizzaSizeBtnClicked={props.onPizzaSizeBtnClicked}
          onLeftArrowClicked={props.onLeftArrowClicked} onRightArrowClicked={props.onRightArrowClicked} key={item.src + '_' + item.size} />
        })}
      </>
    );
}

class PizzaMenuContainer extends React.Component {
  render() {
    return <PizzaMenu {...this.props} />
  }
}

const mstp = state => {
  return {
    pizzaMenu: state.menu.pizzaMenu,
  }
};

export default connect(mstp, {})(PizzaMenuContainer);