import React from "react";
import { connect } from "react-redux";
import MenuItem from '../MenuItem/MenuItem';

function BurgerMenu(props) {
    return (
      <>
        {props.burgerMenu.map(item => {
          return <MenuItem src={item.src} name={item.name} price={item.price} description={item.description} key={item.src + '_' + item.price}
          onLeftArrowClicked={props.onLeftArrowClicked} onRightArrowClicked={props.onRightArrowClicked} />
        })}
      </>
    );
}

class BurgerMenuContainer extends React.Component {
  render() {
    return <BurgerMenu {...this.props} />
  }
}

const mstp = state => {
  return {
    burgerMenu: state.menu.burgerMenu,
  }
}

export default connect(mstp, {})(BurgerMenuContainer);