import React from "react";
import { connect } from "react-redux";
import MenuItem from '../MenuItem/MenuItem';

function SandwichMenu(props) {
    return (
      <>
        {props.sandwichMenu.map(item => {
          return <MenuItem src={item.src} name={item.name} price={item.price} description={item.description} key={item.src + '_' + item.price}
          onLeftArrowClicked={props.onLeftArrowClicked} onRightArrowClicked={props.onRightArrowClicked} />
        })}
      </>
    )
}

class SandwichMenuContainer extends React.Component {
  render() {
    return <SandwichMenu {...this.props} />
  }
}

const mstp = state => {
  return {
    sandwichMenu: state.menu.sandwichMenu,
  }
};

export default connect(mstp, {})(SandwichMenuContainer);