import React from "react";
import { connect } from "react-redux";
import MenuItem from '../MenuItem/MenuItem';

function SaladMenu(props) {
    return (
      <>
        {props.saladMenu.map(item => {
          return <MenuItem src={item.src} name={item.name} price={item.price} description={item.description} key={item.src + '_' + item.price}
          onLeftArrowClicked={props.onLeftArrowClicked} onRightArrowClicked={props.onRightArrowClicked} />
        })}
      </>
    );
}

class SaladMenuContainer extends React.Component {
  render() {
    return <SaladMenu {...this.props} />
  }
}

const mstp = state => {
  return {
    saladMenu: state.menu.saladMenu,
  }
};

export default connect(mstp, {})(SaladMenuContainer);