import React from "react";
import './closeHeaderBtn.css';

export default function CloseHeaderBtn() {
    const onCloseHeaderBtnClicked = () => {
        if (document.documentElement.scrollTop <= 60) return;

        const h = document.getElementById('header');
        h.style.transform = 'translateX(-100%)';
    };

    return <div onClick={onCloseHeaderBtnClicked} className="close-header-btn-container">
    <button id="close-header-btn">&#8592;</button>
</div>
}