const BLUR_PAGE = 'BLUR_PAGE';

export const blurPageAC = isBlured => ({type: BLUR_PAGE, isBlured});

const initialState = {
    isBlured: false,
};

const pageReducer = (state = initialState, action) => {
    switch(action.type) {
        case BLUR_PAGE: {
            return {
                ...state,
                isBlured: action.isBlured,
            }
        }
        default: {
            return state;
        }
    }
};

export default pageReducer;