const TOGGLE_SHOPPING_CART_DISPLAY = 'TOGGLE_SHOPPING_CART_DISPLAY';
const TOGGLE_ADD_NEW_ITEM_NOTIFICATION = 'TOGGLE_ADD_NEW_ITEM_NOTIFICATION';
const TOGGLE_MOBILE_NAVIGATION = 'TOGGLE_MOBILE_NAVIGATION'; 

export const toggleShoppingCartDisplayAC = val => ({type: TOGGLE_SHOPPING_CART_DISPLAY, val});
export const toggleAddNewItemNotificationAC = val => ({type: TOGGLE_ADD_NEW_ITEM_NOTIFICATION, val});
export const toggleMobileNavigationAC = val => ({type: TOGGLE_MOBILE_NAVIGATION, val});

const initialState = {
    shoppingCart: {
        isOpen: false,
        addedNewItem: false,
    },

    nav: {
        mobNavbarIsClicked: false,
    }
}

const headerReducer = (state = initialState, action) => {
    switch(action.type) {
        case TOGGLE_SHOPPING_CART_DISPLAY: {
            let stateCopy = {...state};
            stateCopy.shoppingCart.isOpen = action.val;
            return stateCopy;
        }
        case TOGGLE_ADD_NEW_ITEM_NOTIFICATION: {
            let stateCopy = {...state};
            stateCopy.shoppingCart.addedNewItem = action.val;
            return stateCopy;
        }
        case TOGGLE_MOBILE_NAVIGATION: {
            let stateCopy = {...state};
            stateCopy.nav.mobNavbarIsClicked = action.val;
            return stateCopy;
        }
        default: {
            return state;
        }
    }
}

export default headerReducer;