import React from "react";
import './PizzaOfTheMonth.css';
import PizzaOfTheMonthImg from '../images/pizza-of-the-month.jpg';
import heartIcon from '../icons/heart.png';

function PizzaOfTheMonth() {
    return (
      <div id="pizza-of-the-month-main-container" className='pizza-of-the-month-main-container'>
        <div className='pizza-of-the-month-wrapper'>
          <img
          src={PizzaOfTheMonthImg} alt='pizza-of-the-month' className='pizza-bestseller-img' />
          <div className='pizza-of-the-month-description'>
            <h3 className='pizza-of-the-month-title'>This pizza has become your favourite again!</h3>
            <p className='pizza-of-the-month-content'>
              It won the hearts of many people and it has been at the top of the list for a long time.
              BBQ chicken pizza has become a bestseller again! And for good reason, because it is so appetizing and delicious. Have not tried it yet?  
            </p>
            <p className='pizza-of-the-month-price-description'>
              For only <span className='pizza-of-the-month-price'>$9,95</span> you will taste this delicious pizza. It is worth it.
            </p>
            <img className='heart-icon' src={heartIcon} alt='heart' />
          </div>
        </div>
      </div>
    );
}

export default PizzaOfTheMonth;