import React from "react";
import { connect } from "react-redux";
import CloseHeaderBtn from "../CloseHeaderBtn/CloseHeaderBtn";
import { toggleMobileNavigationAC } from "../header-reducer/header-reducer";
import './Navbar.css';

function Navbar() {
  const onMenuClicked = () => {
    const mobNav = document.querySelector('.navigation-main-container');

    mobNav.style.transform = mobNav.style.transform === 'translateX(0%)' ? 'translateX(-100%)' : 'translateX(0%)';
  }

    return (
      <nav>
        <div className="menu-toggle-container">
          <div id="menu-toggle">
            <input onClick={onMenuClicked} type="checkbox" />
            <span></span>
            <span></span>
            <span></span>
          </div>
        </div>
        <div className="navigation-main-container">
          <ul className='navigation-container'>
            <li className='navbar-item'><a className='navbar-item-link' href='#root'>Home</a></li>
            <li className='navbar-item'><a className='navbar-item-link' href='#main-content-middle-wrapper'>Why us</a></li>
            <li className='navbar-item'><a className='navbar-item-link' href='#pizza-of-the-month-main-container'>Bestseller</a></li>
            <li className='navbar-item'><a className='navbar-item-link' href='#menu'>Menu</a></li>
            <li className='navbar-item'><a className='navbar-item-link' href='#contact'>Contact</a></li>
            <li className='navbar-item'><a className='navbar-item-link' href='#where-to-find-us-wrapper'>Location</a></li>
            <li className='navbar-item'><a className='navbar-item-link' href='#footer'>Follow</a></li>
          </ul>
          <CloseHeaderBtn />
        </div>
      </nav>
    )
  }

  class NavbarContainer extends React.Component {
    render() {
      return <Navbar {...this.props} />
    }
  }

  const mstp = state => {
    return {
      mobNavbarIsClicked: state.header.nav.mobNavbarIsClicked,
    }
  }

  export default connect(mstp, {
    toggleMobileNavigation: toggleMobileNavigationAC,
  })(NavbarContainer);