import React, { useState } from "react";
import './Menu.css';
import PizzaMenuContainer from '../PizzaMenu/PizzaMenu';
import BurgerMenuContainer from '../BurgerMenu/BurgerMenu';
import SaladMenuContainer from '../SaladMenu/SaladMenu';
import SandwichMenuContainer from '../SandwichMenu/SandwichMenu';
import DrinksMenuContainer from "../DrinksMenu/DrinksMenu";

function Menu(props) {
    const [menuNumber, setMenuNumber] = useState(0);
    return (
      <div className='menu-container' id='menu'>
        <ul className='menu-toggle-list' onClick={e => props.onMenuToggleListClicked(e, setMenuNumber)}>
          <li className='pizzas-toggle-btn menu-toggle-list-item menu-list-item-selected'>Pizzas</li> 
          <li className='burgers-toggle-btn menu-toggle-list-item'>Burgers</li> 
          <li className='salads-toggle-btn menu-toggle-list-item'>Salads</li> 
          <li className='sandwiches-toggle-btn menu-toggle-list-item'>Sandwiches</li> 
          <li className='drinks-toggle-btn menu-toggle-list-item'>Drinks</li>  
        </ul>
        <ul className='menu-list'>
          { menuNumber === 0 && <PizzaMenuContainer onPizzaSizeBtnClicked={props.onPizzaSizeBtnClicked} onLeftArrowClicked={props.onLeftArrowClicked} 
          onRightArrowClicked={props.onRightArrowClicked} /> }
          { menuNumber === 1 && <BurgerMenuContainer onLeftArrowClicked={props.onLeftArrowClicked} onRightArrowClicked={props.onRightArrowClicked} /> }
          { menuNumber === 2 && <SaladMenuContainer onLeftArrowClicked={props.onLeftArrowClicked} onRightArrowClicked={props.onRightArrowClicked} /> }
          { menuNumber === 3 && <SandwichMenuContainer onLeftArrowClicked={props.onLeftArrowClicked} onRightArrowClicked={props.onRightArrowClicked} /> }
          { menuNumber === 4 && <DrinksMenuContainer onLeftArrowClicked={props.onLeftArrowClicked} onRightArrowClicked={props.onRightArrowClicked} /> }
        </ul>
      </div>
    );
}

class MenuContainer extends React.Component {
  onLeftArrowClicked(e, setNumberOfDishes) {
    return +e.target.nextElementSibling.textContent >= 1 && setNumberOfDishes(--e.target.nextElementSibling.textContent);
  }

  onRightArrowClicked(e, setNumberOfDishes) {
    return setNumberOfDishes(++e.target.previousElementSibling.textContent);
  }

  onPizzaSizeBtnClicked(e, setSize) {
    if (e.target.tagName === 'DIV' || e.target.classList.contains('selected')) return;

    let target = e.target;

    if (target.tagName !== 'BUTTON') {
      target = e.target.closest('button');
    }

    const children = Array.from(e.currentTarget.children);
    children.forEach(btn => {
      if (btn.classList.contains('selected')) {
        btn.classList.remove('selected');
      }

      target.classList.add('selected');
    });

    setSize(parseInt(e.target.textContent));
  }

  onMenuToggleListClicked(e, setMenuNumber) {
    if (e.target.tagName === 'UL') return;

    let target = e.target;

    if (target.tagName !== 'LI') {
      target = e.target.closest('li');
    }

    const children = Array.from(e.currentTarget.children);

    const currentIndex = children.findIndex(el => target.classList[0] === el.classList[0]);

    setMenuNumber(currentIndex);

    children.forEach(child => {
      if (child.classList.contains('menu-list-item-selected')) child.classList.remove('menu-list-item-selected');
    });
    target.classList.add('menu-list-item-selected');
  }

  render() {
    return <Menu onLeftArrowClicked={this.onLeftArrowClicked} onRightArrowClicked={this.onRightArrowClicked} onPizzaSizeBtnClicked={this.onPizzaSizeBtnClicked} 
    onMenuToggleListClicked={this.onMenuToggleListClicked} />
  }
}

export default MenuContainer;