import React from "react";
import { connect } from "react-redux";
import { toggleShoppingCartDisplayAC } from "../header-reducer/header-reducer";
import { blurPageAC } from "../page-reducer/page-reducer";
import shoppingCartIcon from '../icons/carts.png';
import './ShoppingCartIcon.css';

function ShoppingCartIcon(props) {
    return (
      <> 
        <div className={`shopping-cart-icon-wrapper ${props.addNewItem ? 'added-item' : ''}`} 
        onClick={props.onShoppingCartIconClicked}>
          <img unselectable="on" className='shopping-cart-icon' src={shoppingCartIcon} alt='shopping-cart-icon' />
        </div>
      </>
    );
  }

  class ShoppingCartIconContainer extends React.Component {    
    onShoppingCartIconClicked = () => {
      this.props.toggleShoppingCartDisplay(true);
      this.props.blurPage(true);
    }
    
    render() {
      return <ShoppingCartIcon {...this.props} onShoppingCartIconClicked={this.onShoppingCartIconClicked} />
    }
  }

  const mstp = state => {
    return {
      addNewItem: state.header.shoppingCart.addedNewItem,
    }
  };

  export default connect(mstp, {
    blurPage: blurPageAC,
    toggleShoppingCartDisplay: toggleShoppingCartDisplayAC,
  })(ShoppingCartIconContainer);