import React from "react";
import './MiddleWrapper.css';
import PizzaOfTheMonth from '../PizzaOfTheMonth/PizzaOfTheMonth';
import MenuContainer from '../Menu/Menu';
import WhyUsContainer from "../WhyUs/WhyUs";

function MiddleWrapper() {
    return (
      <>
        <div id="main-content-middle-wrapper" className='main-content-middle-wrapper'>
          <WhyUsContainer />
          <PizzaOfTheMonth />
          <MenuContainer />
        </div>
      </>
    );
}

export default MiddleWrapper;