import React from "react";
import './Header.css';
import Logo from '../Logo/Logo';
import ShoppingCartIconContainer from '../ShoppingCartIcon/ShoppingCartIcon';
import NavbarContainer from "../Navbar/Navbar";
import OpenHeaderBtn from "../OpenHeaderBtn/OpenHeaderBtn";


function Header() {
    return (
      <header id="header">
        <NavbarContainer />
        <Logo />
        <ShoppingCartIconContainer />
        <OpenHeaderBtn />
      </header>
    );
}

export default Header;