import React, { useState } from "react";
import { connect } from "react-redux";
import Discount from "../Discount/Discount";
import { toggleAddNewItemNotificationAC } from "../header-reducer/header-reducer";
import { addItemAC } from "../menu-reducer/menu-reducer";
import './MenuItem.css';

function MenuItem(props) {
    const [size, setSize] = useState(parseInt(props.size));
    const [numberOfDishes, setNumberOfDishes] = useState(0);
    const price = (size === 25 ? props.minPrice : size === 30 ? props.mediumPrice : size === 35 ? props.maxPrice : props.price || '--').toString();

    const touchScreen = 'ontouchstart' in document;

    const setOpacity = (target, opacity) => target.style.opacity = opacity;

    const onMouseDown = e => setOpacity(e.currentTarget, '0.25');
    const onMouseUp = e => setOpacity(e.currentTarget, '1');
    const onMouseLeave = e => setOpacity(e.currentTarget, '1');

    const onBtnClicked = e => {
      const target = e.currentTarget;

      if (touchScreen) {
        target.style.transitiopProperty = 'opacity';
        target.style.transitionDuration = '100ms';
        target.style.transitionTimingFunction = 'ease';
      }

      setOpacity(target, '0.25');
      setTimeout(() => setOpacity(target, '1'), touchScreen ? 100 : 50);
    }

    const onAddToCartBtnClicked = e => {
      props.addToShoppingCart(e, props, numberOfDishes, size);

      onBtnClicked(e);
    };

    const onLeftArrowBtnClicked = e => {
      props.onLeftArrowClicked(e, setNumberOfDishes);

      onBtnClicked(e);
    };

    const onRightArrowBtnClicked = e => {
      props.onRightArrowClicked(e, setNumberOfDishes);

      onBtnClicked(e);
    };

    return (
      <div className='menu-list-item-container'>
        <img unselectable="on" draggable='false' src={props.src} style={props.imgStyles} alt={props.name} className='menu-list-image' />
        <li className='menu-list-item-name'>{props.name}</li>
        <p className='menu-list-item-description'>{props.description}</p>
        <div className='menu-list-item-size-container' onClick={e => props.itemIsPizza && props.onPizzaSizeBtnClicked(e, setSize)}>
          { props.itemIsPizza && <button className='pizza-size selected'>25cm</button> }
          { props.itemIsPizza && <button className='pizza-size'>30cm</button> }
          { props.itemIsPizza && <button className='pizza-size'>35cm</button> } 
        </div>
        <div className='counter-wrapper'>
          <div className="arrows-container">
            <button className='left-arrow counter-item' 
            onMouseDown={e => !touchScreen && onMouseDown(e)} onMouseUp={e => !touchScreen && onMouseUp(e)} onMouseLeave={onMouseLeave}
            onClick={onLeftArrowBtnClicked}>&#8722;</button>
            <div className='number counter-item'>{numberOfDishes}</div>
            <button className='right-arrow counter-item' 
            onMouseDown={e => !touchScreen && onMouseDown(e)} onMouseUp={e => !touchScreen && onMouseUp(e)} onMouseLeave={onMouseLeave}
             onClick={onRightArrowBtnClicked}>&#43;</button>
          </div>
          <button onMouseDown={e => !touchScreen && onMouseDown(e)} onMouseUp={e => !touchScreen && onMouseUp(e)} 
          onMouseLeave={onMouseLeave}
           className='add-to-shopping-cart-btn' onClick={onAddToCartBtnClicked}>Add</button>  
        </div>
        <div className='menu-list-item-price'>
          {'$' + price.replace('.', ',')}
          {props.onSale && <Discount />}
        </div>
      </div>
    );
}

class MenuItemContainer extends React.Component {
  addToShoppingCart(e, props, numberOfDishes, size) {
    if (numberOfDishes < 1) return;
    if (props.itemIsPizza) {
      const pizzaPrice = size === 25 ? props.minPrice : size === 30 ? props.mediumPrice : size === 35 ? props.maxPrice : ''
      props.addItem({
        src: props.src,
        name: props.name,
        size: size + 'cm',
        totalCount: numberOfDishes,
        price: pizzaPrice,
        onSale: props.onSale,
        isPizza: props.itemIsPizza,
        id: props.src + '_' + pizzaPrice,
      })
    } else {
      props.addItem({
        src: props.src,
        name: props.name,
        totalCount: numberOfDishes,
        price: props.price,
        onSale: props.onSale,
        isPizza: props.itemIsPizza,
        id: props.src + '_' + props.price,
      });
    }
    props.toggleAddNewItemNotification(true);
  }

  render() {
    return <MenuItem {...this.props} addToShoppingCart={this.addToShoppingCart}
    onMenuItemHovered={this.onMenuItemHovered} onMenuItemUnhovered={this.onMenuItemUnhovered} />
  }
}

export default connect(null, {
  addItem: addItemAC,
  toggleAddNewItemNotification: toggleAddNewItemNotificationAC,
})(MenuItemContainer);