import React from "react";
import './Footer.css';
import Logo from '../Logo/Logo';
import instagramIcon from '../icons/instagram.png';
import twitterIcon from '../icons/twitter.png';
import facebookIcon from '../icons/facebook.png';
import youtubeIcon from '../icons/youtube.png';

function Footer() {
    return (
      <footer id="footer">
        <h3 className='footer-title'>Follow us</h3>
        <ul className='follow-us-list'>
          <li className='follow-us-list-item'>
            <img unselectable="on" src={instagramIcon} alt='instagram-icon' className='follow-us-icon' />
          </li>
          <li className='follow-us-list-item'>
            <img unselectable="on" src={twitterIcon} alt='twitter-icon' className='follow-us-icon' />
          </li>
          <li className='follow-us-list-item'>
            <img unselectable="on" src={facebookIcon} alt='facebook-icon' className='follow-us-icon' />
          </li>
          <li className='follow-us-list-item'>
            <img unselectable="on" src={youtubeIcon} alt='youtube-icon' className='follow-us-icon' />
          </li>   
        </ul>
        <Logo />
        <p className='work-schedule'>Service: Monday - Saturday , 09:00 - 22:00</p>
        <p className='copyright'>Copyright &copy; {new Date().getFullYear()} Funpizza. All rights reserved.</p> 
      </footer>
    )
}

export default Footer;