const menuReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_ITEM: {
            let stateCopy = {...state};
            let items = stateCopy.addedItems;

            for(let i = 0; i < items.length; i++) {
                if (items[i].id === action.item.id) {
                    items[i] = action.item;
                    return stateCopy;
                }
            }

            stateCopy.addedItems = [...state.addedItems, action.item];
            return stateCopy;
        }
        case CLEAR_SHOPPING_CART: {
            return {
                ...state,
                addedItems: [],
            }
        }
        case DELETE_ITEM_FROM_SHOPPING_CART: {
            return {
                ...state,
                addedItems: state.addedItems.filter(item => item.id !== action.id),
            }
        }
        default: {
            return state;
        }
    }
};

export default menuReducer;