import React from "react";
import './FirstMenuLook.css';
import pizzaIcon from '../icons/pizza.png';
import burgerIcon from '../icons/burger.png';
import saladIcon from '../icons/salad.png';
import sandwichIcon from '../icons/sandwich.png';

function FirstMenuLook() {  
    return (
      <ul className='first-menu-look-list'>
        <div className='first-menu-look-list-item-wrapper'>
          <li className='first-menu-look-list-item'>Pizzas</li>
          <img unselectable="on" className='first-menu-look-list-item-icon' src={pizzaIcon} alt='pizza-icon' />
        </div>
        <div className='first-menu-look-list-item-wrapper'>
          <li className='first-menu-look-list-item'>Burgers</li>
          <img unselectable="on" className='first-menu-look-list-item-icon' src={burgerIcon} alt='burger-icon' />
        </div>
        <div className='first-menu-look-list-item-wrapper'>
          <li className='first-menu-look-list-item'>Salads</li>
          <img unselectable="on" className='first-menu-look-list-item-icon' src={saladIcon} alt='salad-icon' />
        </div>
        <div className='first-menu-look-list-item-wrapper'>
          <li className='first-menu-look-list-item'>Sandwiches</li>
          <img unselectable="on" className='first-menu-look-list-item-icon' src={sandwichIcon} alt='sandwich-icon' />
        </div>                
      </ul>
    );
}

export default FirstMenuLook;