import React from "react";
import './WhereToFindUs.css';
import locationIcon from '../icons/location.png';
import phoneIcon from '../icons/phone.png';
import gmailIcon from '../icons/gmail.png';

function WhereToFindUs() {
    return (
      <div id="where-to-find-us-wrapper" className='where-to-find-us-wrapper'>
        <h2 className='where-to-find-us-title'>Where to find us</h2>
        <ul className='where-to-find-us-list'>
          <div className='where-to-find-us-list-item-wrapper'>
            <img unselectable="on" src={locationIcon} alt='location-icon' className='where-to-find-us-icon' />
            <li className='where-to-find-us-list-item'>1495 Pizza street, New York, USA</li>
          </div>
          <div className='where-to-find-us-list-item-wrapper'>
            <img unselectable="on" src={phoneIcon} alt='phone-icon' className='where-to-find-us-icon' />
            <li className='where-to-find-us-list-item'>+123 45 678 901</li>
          </div>
          <div className='where-to-find-us-list-item-wrapper where-to-find-us-last-list-item-wrapper'>
            <img unselectable="on" src={gmailIcon} alt='gmail-icon' className='where-to-find-us-icon' />
            <li className='where-to-find-us-list-item'>funpizza@gmail.com</li>
          </div>                            
        </ul>  
      </div>
    )
}

export default WhereToFindUs;