import React from "react";
import './ContactUs.css';
import contactUsBackground from '../images/pizza-img3.jpg';
import swal from "sweetalert";

function ContactUs() {
  const onFormSubmit = e => {
    e.preventDefault();
    swal("Thank you for your message!", `We will contact you as soon as possible.`, "success");
  }

    return (
      <div className='contact-us-wrapper'>
        <img unselectable="on" className='contact-us-background' src={contactUsBackground} alt='contact-us-background' />
        <form onSubmit={onFormSubmit} className='contact-us-form'>
          <h2 className='contact-us-main-title'>Contact us</h2>
          <h3 className='contact-us-title'>Do you have any question or suggest?</h3>
          <input placeholder='Your name' required type='text' className='contact-us-input' />
          <input placeholder='Your email' type='email' className='contact-us-input' />
          <input required type='tel' className='contact-us-input' 
          pattern="\+7\s?[\(]{0,1}9[0-9]{2}[\)]{0,1}\s?\d{3}[-]{0,1}\s?\d{2}[-]{0,1}\s?\d{2}" placeholder="Your tel -> +7 (xxx) xxx-xx-xx" />
          <textarea required className='contact-us-input contact-us-textarea' placeholder='write here a message'></textarea>
          <button className='contact-us-submit-btn'>Submit</button>
        </form>
      </div>
    )
}

export default ContactUs;