import React from "react";
import './openHeaderBtn.css';

export default function OpenHeaderBtn() {
    const onOpenHeaderBtnClicked = () => {
        const h = document.getElementById('header');

        h.style.transform = 'translateX(0px)';
    };

    return <div onClick={onOpenHeaderBtnClicked} className="open-header-btn-container">
        <button id="open-header-btn">&#8594;</button>
    </div>
}