import React from "react";
import './TopWrapper.css';
import PizzaImg from '../PizzaImg/PizzaImg';
import MainTitle from '../MainTitle/MainTitle';
import FirstMenuLook from '../FirstMenuLook/FirstMenuLook';

function TopWrapper() {
    return (
      <>
        <div className="main-content-top-wrapper">
          <PizzaImg />
          <MainTitle />
          <FirstMenuLook />
          <p className='end-description-text'>Let's go!</p>
        </div>
      </>
    );
}

export default TopWrapper;