import React from "react";
import './WhyUs.css';
import pizzaImg1 from '../images/pizza_img1.jpg';
import thumbsUpImg from '../images/thumbs_up.jpg';
import pizzaImg2 from '../images/pizza_img2.jpeg';
import chefImg from '../images/chef.jpg';
import deliveryImg from '../images/delivery.jpg';
import timeImg from '../images/time.jpg';

function WhyUs() {
    return (
      <>
      <p className='middle-wrapper-title'>Why us?</p>
        <ul className='why-us-list'>
          <div className='why-us-list-item-container'>
            <img unselectable="on" src={pizzaImg1} className='why-us-image' alt='tasty-pizza' />
            <li className='why-us-list-item'>Our pizza is the tastiest and it is made with fresh ingredients</li>
          </div>
          <div className='why-us-list-item-container'>
            <img unselectable="on" src={thumbsUpImg} className='why-us-image' alt='thumbs-up' />
            <li className='why-us-list-item'>Good price. You will definitely like it</li> 
          </div>
          <div className='why-us-list-item-container'>
            <img unselectable="on" src={pizzaImg2} className='why-us-image' alt='pizza-with-friends' />
            <li className='why-us-list-item'>Awesome atmosphere. You can have a cool time with your friends</li>  
          </div>
          <div className='why-us-list-item-container'>
            <img unselectable="on" src={chefImg} className='why-us-image' alt='chef' />
            <li className='why-us-list-item'>Our wonderful chefs make pizza with love</li>  
          </div>
          <div className='why-us-list-item-container'>
            <img unselectable="on" src={deliveryImg} className='why-us-image' alt='delivery' />
            <li className='why-us-list-item'>We have home delivery</li>  
          </div>  
          <div className='why-us-list-item-container'>
            <img unselectable="on" src={timeImg} className='why-us-image' alt='time' />
            <li className='why-us-list-item'>You will get your pizza in time</li>  
          </div>            
        </ul>
      </>
    );
}

export default WhyUs;