import React from "react";
import { connect } from "react-redux";
import MenuItem from "../MenuItem/MenuItem";

function DrinksMenu(props) {
    return (
        <>
        {props.drinksMenu.map(item => {
            return <MenuItem src={item.src} name={item.name} price={item.price} description={item.description} 
            onLeftArrowClicked={props.onLeftArrowClicked} onRightArrowClicked={props.onRightArrowClicked} key={item.src + '_' + item.price} />
        })}
        </>
    );
}

class DrinksMenuContainer extends React.Component {
    render() {
        return <DrinksMenu {...this.props} />
    }
}

const mstp = state => {
    return {
        drinksMenu: state.menu.drinks,
    }
}

export default connect(mstp, {})(DrinksMenuContainer);

