import React from "react";
import './Logo.css';
import logoImg from '../icons/logo.png';

function Logo() {
  const onImgLoad = e => e.currentTarget.classList.add('logo-animation'); 
    return (
      <div className='logo-wrapper'>
        F<img onLoad={onImgLoad} unselectable="on" className='logo' src={logoImg} alt='logo' />n pizza
      </div>
    )
}

export default Logo;